import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/Moderator/FinanceModerator.module.css";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import AdmissionModeratoMenu from "./AdmissionModeratoMenu";
import { useState } from "react";

const AdmissionModeratorCard = ({ moderator, instituteId, aid, onRefetch }) => {
  const { t } = useTranslation();
  const [moderatorMenu, setModeratorMenu] = useState("");

  const onMenu = () => {
    setModeratorMenu(moderator);
  };
  const onClose = () => {
    setModeratorMenu("");
  };
  return (
    <>
      <div className={style.moderator_card}>
        <img
          src={
            moderator?.access_staff?.staffProfilePhoto
              ? `${imageShowUrl}/${moderator?.access_staff?.staffProfilePhoto}`
              : `${assestsFeedUrl}/feed-user-avatar.svg`
          }
          alt="moderator avatar"
        />
        <section className={style.moderator_card_text_container}>
          <h6>
            {`${moderator?.access_staff?.staffFirstName} ${
              moderator?.access_staff?.staffMiddleName ?? ""
            } ${moderator?.access_staff?.staffLastName}`}
          </h6>
          <p>
            {t("index_number")} {" : "}
            {moderator?.access_staff?.staffROLLNO}
          </p>
          <p>
            {
              // moderator?.access_role === "ALL_FEE_ACCESS"
              //   ? "Fee receipt authority"
              //   :
              moderator?.access_role === "MULTI_APP_ACCESS"
                ? "Application Manager"
                : moderator?.access_role === "ONGOING_ACCESS"
                ? "All Application Manager"
                : moderator?.access_role === "ONGOING_VIEW_ACCESS"
                ? "All Application Manager (Only View)"
                : moderator?.access_role === "INQUIRY_ACCESS"
                ? "Enquiries Section"
                : moderator?.access_role === "COMBINED_APP_ACCESS"
                ? "Combined Application"
                : //  moderator?.access_role === "REFUND_ACCESS"
                // ? "Access Fee Moderator"
                // : moderator?.access_role === "PENDING_FEE_ACCESS"
                // ? "Pending Fee Manager"
                // :
                moderator?.access_role === "ONE_TAB_ACCESS"
                ? moderator?.active_tab?.role === "Application"
                  ? "Admission Application Tab"
                  : moderator?.active_tab?.role === "Selected"
                  ? "Admission Document Tab"
                  : moderator?.active_tab?.role === "Confirmed"
                  ? "Admission Confirmed Tab"
                  : moderator?.active_tab?.role === "Allotted"
                  ? "Admission Allotted Tab"
                  : moderator?.active_tab?.role === "FEE COLLECTION TAB"
                  ? "Admission Fee Collection Tab"
                  : moderator?.active_tab?.role === "REVIEW TAB"
                  ? "Admission Review Tab"
                  : "Admission Cancelled Tab"
                : moderator?.access_role === "DATA_EXPORT_ACCESS"
                ? "Data Export Authority"
                : moderator?.access_role === "REQUIRED_DOCUMENTS"
                ? "Document Co-ordinator"
                : ""
            }
          </p>
        </section>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onMenu}
          className={style.menu_icon}
          title="Menu"
        />
      </div>
      {moderatorMenu && (
        <AdmissionModeratoMenu
          onClose={onClose}
          aid={aid}
          openModeratorMenu={moderatorMenu}
          instituteId={instituteId}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default AdmissionModeratorCard;
